/* Background and layout */
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f8ff; /* Light, friendly background color */
  min-height: 100vh;
  height: 100vh;             /* Full height of the viewport */
  padding: 0;                /* Remove any padding to use full space */
  box-sizing: border-box;    /* Ensure border-box includes padding in height/width calculations */
}

/* Chat area */




/* Chat bubbles */
.chat-bubble {
  padding: 10px 15px;
  margin: 8px 0;
  border-radius: 20px;
  max-width: 80%;
  word-wrap: break-word;
}

.student-message {
  background-color: #dcedc8; /* Lively green for student messages */
  align-self: flex-end;
  text-align: right;
}

.chatbot-message {
  background-color: #cccccc; /* Soft yellow for bot messages */
  align-self: flex-start;
  text-align: left;
}

/* Claim section */
.claim {
  text-align: left;
  width: 80%;
  font-size: 1.4rem;
  font-weight: bold;
  margin-bottom: 20px;
  color: #37474f; /* Darker gray for readability */
  background-color: #fffbe7;
  padding: 15px;
  border-radius: 8px;
  border: 1px solid #cccccc;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.1);
}

/* Input Area Flexbox for Textarea and Button */
.input-area {
  display: flex;
  align-items: center;
  width: 80%;              /* Match the width of the chatbox */
  margin: 0px auto;         /* Center the input area */
  padding: 0;               /* Remove any padding */
  box-sizing: border-box;    /* Ensure padding doesn't affect width */
}

textarea {
  flex-grow: 1;             /* Make the textarea take up remaining space */
  height: 100px;            /* Fixed height */
  padding: 15px;
  border-radius: 10px;
  border: 2px solid #cccccc;
  background-color: #fff;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  font-size: 1rem;
  resize: none;             /* Prevent resizing */
  margin-right: 10px;       /* Space between the textarea and button */
}

/* Send button styled with greenish color */
button {
  padding: 12px;
  background-color: #8bc34a;  /* Green color to match the student chat bubble */
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 50%;   /* Circular button */
  font-size: 1.5rem;    /* Larger font size for the icon */
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;          /* Square button */
  height: 50px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #689f38;  /* Slightly darker green on hover */
  animation: bounce 0.5s; 
}

/* Chat Area */
.conversation {
  background-color: #ffffff;
  padding: 20px;
  flex-grow: 1;               /* Grow to fill available space */
  /*height: 400px;*/
  width: 80%;                /* Match width with input area */
  overflow-y: auto;
  margin-bottom: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;     /* Set flex-direction to column */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); /* Light shadow for depth */
  border: 2px solid #cccccc;  /* Soft, colorful border */
  border: 2px solid #cccccc;  /* Soft, colorful border */
  max-height: calc(100vh - 400px); /* Prevent the conversation area from getting too tall */

}

