.attached-image {
  max-width: 250px;    /* Limit the width to 150px */
  max-height: 250px;   /* Limit the height to 150px */
  margin-top: 10px;    /* Add some space between the image and the text */
  border-radius: 8px;  /* Optional: make the corners rounded */
  object-fit: cover;   /* Ensure the image keeps its aspect ratio and fits within the bounds */
  cursor: pointer;     /* Change cursor to indicate that the image can be interacted with */
}


/* Input container to hold textarea and buttons */
.input-container {
  position: relative;
  width: 80%;
  margin: 20px auto;         /* Center the input container */
  display: flex;
  align-items: center;
}

textarea {
  width: 100%;
  height: 100px;
  padding: 15px 50px 15px 50px; /* Space for icons on both sides */
  border-radius: 25px;
  border: 2px solid #cccccc;
  background-color: #fff;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  font-size: 1rem;
  resize: none;             /* Prevent resizing */
  box-sizing: border-box;   /* Ensure padding and border are included in width */
}

/* Style for paperclip and send button */
.icon-button {
  position: absolute;
  background: none;
  border: none;
  cursor: pointer;
  color: #8bc34a;          /* Green to match the theme */
  font-size: 1.5rem;
  padding: 10px;
  transition: color 0.1s ease, background-color 0.1s ease; /* Smooth transition */
}

.icon-button:hover {
  background-color: #8bc34a;  /* Green background on hover */
  color: white;               /* Change the icon color to white on hover */
}

.attach-button {
  left: 10px;              /* Position on the left inside the textarea */
}

.send-button {
  right: 10px;             /* Position on the right inside the textarea */
}

