/* Button hover effect for consistency */
.icon-button:hover {
  color: #689f38; /* Slightly darker green on hover */
}

/* Tooltip styles */
.icon-button[data-tooltip]::before {
  content: attr(data-tooltip);  /* Use the value of data-tooltip as content */
  position: absolute;
  bottom: 130%;                 /* Position the tooltip above the button */
  left: 50%;
  transform: translateX(-50%);
  background-color: #333;       /* Dark background for the tooltip */
  color: #fff;                  /* White text for readability */
  padding: 5px 10px;
  border-radius: 5px;
  white-space: nowrap;
  font-size: 1.3rem;
  opacity: 0;
  pointer-events: none;         /* Prevent the tooltip from blocking interactions */
  transition: opacity 0.5s ease-in-out;  /* Quick transition for appearing */
  z-index: 10;                  /* Make sure tooltip appears above other elements */
}

/* Tooltip arrow */
.icon-button[data-tooltip]::after {
  content: '';
  position: absolute;
  bottom: 120%;
  left: 50%;
  transform: translateX(-50%);
  border-width: 5px;
  border-style: solid;
  border-color: #333 transparent transparent transparent; /* Arrow pointing down */
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  z-index: 10;
}

/* Show tooltip on hover */
.icon-button:hover::before,
.icon-button:hover::after {
  opacity: 1;                  /* Make the tooltip visible */
}

.clear-button {
  right: 60px;             /* Position next to the send button, allowing space between */
}